/* Import Google Fonts */

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

/* Apply a CSS reset */

* {
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
}

.contactus-heading {
    text-align: center;
    margin-top: 40px;
    /* Add spacing from the top */
}

.location {
    width: 80%;
    margin: auto;
    padding: 40px 0;
    background-color: #000;
    /* Dark background color for the map container */
}

.location iframe {
    width: 100%;
    filter: invert(1);
    /* Invert the map colors */
}

.row {
    margin-top: 5%;
    display: flex;
    justify-content: space-between;
}

.contact-us {
    width: 80%;
    margin: auto;
    padding: 40px;
    /* Add padding for spacing */
}

.contact-col {
    flex-basis: 48%;
    margin-bottom: 30px;
}

.contact-col div {
    display: flex;
    align-items: center;
    margin-bottom: 40px;
}

.contact-col div .fa {
    font-size: 28px;
    color: #00cc00;
    /* Adjust icon color for the dark theme */
    margin: 10px;
    margin-right: 30px;
}

.contact-col div p {
    padding: 0;
    color: #aaa;
    /* Lighter text color */
}

.contact-col div h5 {
    font-size: 20px;
    margin-bottom: 5px;
    color: #ccc;
    /* Lighter text color */
    font-weight: 400;
}

.contact-col div h5,
.contact-col div p {
    color: #000;
    /* Darker text color for address, phone number, and email */
}

.contact-col input,
.contact-col textarea {
    width: 100%;
    padding: 15px;
    margin-bottom: 17px;
    outline: none;
    border: 1px solid #555;
    background-color: #fff;
    /* White background color */
    color: #000;
    /* Black text color */
    border-radius: 5px;
}


/* Optionally, you can style the placeholder text color */

.contact-col input::placeholder,
.contact-col textarea::placeholder {
    color: #555;
    /* Placeholder text color */
}

.contactus-btn {
    display: inline-block;
    text-decoration: none;
    color: #00cc00;
    /* Button text color for dark theme */
    border: 1px solid #00cc00;
    /* Button border color for dark theme */
    padding: 12px 34px;
    font-size: 13px;
    background: transparent;
    position: relative;
    cursor: pointer;
    transition: border-color 0.3s, background-color 0.3s, color 0.3s;
    /* Add smooth transitions */
}

.contactus-btn:hover {
    border-color: #00aa00;
    /* Darker border color on hover */
    background-color: #00aa00;
    /* Darker background color on hover */
    color: #fff;
    /* Light text color on hover */
}